/* What GPT3 container */
.gpt3__whatgpt3 {
  display: flex;
  flex-direction: column;
  padding: 2rem;
  background: var(--color-footer);
  background: radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25))
}

/* What GPT3 features */
.gpt3__whatgpt3-feature {
  display: flex;
}

/* What GPT3 features */
.gpt3__whatgpt3-feature .gpt3__features-container__feature {
  margin: 0;
}

.gpt3__whatgpt3-feature .gpt3__features-container__feature-text { 
  max-width: 700px;
}

.gpt3__whatgpt3-heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 4rem 0 2rem;
}

/* H1 */
.gpt3__whatgpt3-heading h1 {
  font-weight: 800;
  font-size: 34px;
  line-height: 45px;
  max-width: 510px;
}

/* Paragraph */
.gpt3__whatgpt3-heading p {
  color: var(--color-subtext);
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  cursor: pointer;
}

/* Features container */
.gpt3__whatgpt3-container {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2rem;
}

.gpt3__whatgpt3-container .gpt3__features-container__feature {
  flex: 1;
  margin: 1rem;
  min-width: 210px;
  flex-direction: column;
}

/* Media queries */
/* Large devices */
@media screen and (max-width: 850px) {
  .gpt3__whatgpt3-heading {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 0;
  }

  .gpt3__whatgpt3-heading p {
    margin-top: 1rem;
  }
}

/* High devices */
@media screen and (max-width: 650px) {
  .gpt3__whatgpt3-feature .gpt3__features-container__feature {
    flex-direction: column;
  }

  .gpt3__whatgpt3-feature .gpt3__features-container__feature-text {
    margin-top: 0.5rem;
  }

  .gpt3__whatgpt3-heading h1 {
    font-size: 28px;
    line-height: 40px;
  }
}

/* Extremely high devices */
@media screen and (max-width: 350px) {
  .gpt3__whatgpt3-container .gpt3__features-container__feature {
    margin: 1rem 0;
    min-width: 100%;
  }
}