/* Navbar */
.gpt3__navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 6rem;
}

/* Navbar links */
.gpt3__navbar-links {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.gpt3__navbar-links-logo {
  margin-right: 2rem;
}

.gpt3__navbar-links_logo img {
  width: 62.56px;
  height: 16.02px;
}

.gpt3__navbar-links_container {
  margin-left: 6rem;
  display: flex;
}

.gpt3__navbar-sign {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.gpt3__navbar-links_container p, .gpt3__navbar-sign p, .gpt3__navbar-menu_container p {
  color: white;
  font-weight: 500;
  font-size: 17px;
  line-height: 25px;
  margin: 0 1rem;
  cursor: pointer;
}

.gpt3__navbar-links_container p a:hover, .gpt3__navbar-sign p a:hover {
  color: rgb(219, 219, 219);
  transition: 0.5s ease-in-out;
}

/* Sign section */
/* Sign up Button */
.gpt3__navbar-sign button,
.gpt3__navbar-menu button {
  background-color: rgb(255, 72, 32);
  padding: 0.5rem 1rem;
  color: white;
  border-style: none;
  border-radius: 5px;
  font-family: (--font-family);
  font-weight: 500;
  font-size: 17px;
  line-height: 25px;
  cursor: pointer;
}

.gpt3__navbar-sign button:hover {
  background-color: rgb(255, 103, 32);
  transition: 0.5s ease-in-out;
}

/* Sign mobile section */
.gpt3__navbar-menu {
  margin-left: 1rem;
  display: none;
  position: relative;
}

.gpt3__navbar-menu svg {
  cursor: pointer;
}

.gpt3__navbar-menu_container {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;
  text-align: end;
  background: var(--color-footer);
  padding: 2rem;
  position: absolute;
  top: 40px;
  right: 0;
  margin-top: 1rem;
  min-width: 210px;
  border-radius: 5px;
  box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}

.gpt3__navbar-menu_container hr {
  display: none;
}

.gpt3__navbar-menu_container p {
  margin: 1rem;
}

.gpt3__navbar-menu_container-links-sign {
  display: none;
}

/* Media queries */
/* Tablets */
@media screen and (max-width: 1050px) {
  .gpt3__navbar-links_container {
    display: none;
  }

  .gpt3__navbar-menu {
    display: flex;
  }
}

@media screen and (max-width: 700px) {
  .gpt3__navbar {
    padding: 2rem 4rem;
  }
}

/* Mobiles */
@media screen and (max-width: 550px) {
  .gpt3__navbar {
    padding: 2rem;
  }

  .gpt3__navbar-sign {
    display: none;
  }

  .gpt3__navbar-menu_container {
    top: 20px;
  }

  .gpt3__navbar-menu_container-links-sign {
    display: block;
  }

  .gpt3__navbar-menu_container hr {
    display: block;
  }
  
}
